<template>
  <page-content>
    <ptz-container
      ref="player-ptz"
      v-show="showPtz"
      :customClass="ptzActiveClass"
      @click-center="onClickCenter"
      @click-top="onClickTop"
      @click-right="onClickRight"
      @click-bottom="onClickBottom"
      @click-left="onClickLeft"
    >
    </ptz-container>
    <div class="video-play">
      <a-card :bordered="false">
        <EasyPlayer
          video-title="摄像头直播"
          :videoUrl="playUrl"
          ref="player"
          :aspect="aspect"
          :onDemand="true"
          :autoplay="autoplay"
          :muted="muted"
          element-loading-text="加载中..."
          element-loading-background="#000"
          live
          :fluent="fluent"
          :stretch="stretch"
          @ended="onPlayEnded"
          @play="onPlay"
          @error="onPlayError"
        >
        </EasyPlayer>
        <a-tabs default-active-key="1">
          <a-tab-pane key="1">
            <span slot="tab">
              <a-icon type="share" />
              分享地址
            </span>
            <div class="control" v-if="playUrls.length">
              <a-radio-group v-model="mode" :style="{ marginBottom: '8px' }" @change="changePlay">
                <a-radio-button value="0"> ws-flv </a-radio-button>
                <a-radio-button value="1"> http-flv </a-radio-button>
                <a-radio-button value="4"> hls </a-radio-button>
              </a-radio-group>
              <div class="form-group">
                <a-input addon-before="ws-flv" :default-value="playUrls[0]" disabled>
                  <a-icon type="copy" slot="addonAfter" @click="copyPlayUrl(0)" />
                </a-input>
              </div>
              <div class="form-group">
                <a-input addon-before="http-flv" :default-value="playUrls[1]" disabled>
                  <a-icon type="copy" slot="addonAfter" @click="copyPlayUrl(1)" />
                </a-input>
              </div>
              <div class="form-group">
                <a-input addon-before="hls" :default-value="playUrls[4]" disabled>
                  <a-icon type="copy" slot="addonAfter" @click="copyPlayUrl(4)" />
                </a-input>
              </div>
            </div>
          </a-tab-pane>
          <a-tab-pane key="2">
            <span slot="tab">
              <a-icon type="file" />
              录像文件
            </span>
            <a-empty description="程序猿正在迭代中，请耐心等待" />
          </a-tab-pane>
        </a-tabs>
      </a-card>
    </div>
  </page-content>
</template>

<script>
import { ptzCtrl, liveUrl } from '@/api/device'
import EasyPlayer from '@easydarwin/easyplayer'
import { PtzButton } from '../videos/CustomPlayControlBar'
import { PtzContainer } from '@/components/index'
import PageContent from '@c/PageContent'
export default {
    data () {
        return {
            playUrls: [],
            playUrl: '',
            mode: '4',
            aspect: '16:9',
            muted: false,
            autoplay: false,
            fluent: false,
            stretch: false,
            showPtz: false,
            ptzActiveClass: '',
            ptzRequested: false
        }
    },
    components: {
        EasyPlayer,
        PtzContainer,
        PageContent
    },
    mounted () {
      this.liveUrl()
    },
    beforeDestroy () {
        console.log('beforeDestroy')
        this.$refs.player.destroyPlayer()
    },
    methods: {
        addControlBtn () {
            setTimeout(() => {
                const Component = videojs.getComponent('Component')
                PtzButton.setFn((showPtz) => {
                    this.showPtz = showPtz
                    if (!this.showPtz) {
                        this.ptzActiveClass = ''
                    }
                })
                Component.registerComponent('PtzButton', PtzButton)
                const $videoInner = $('.easy-video-player-inner')
                const $videojs = $videoInner.find('.video-js')
                if (!$videojs.length) {
                    return false
                }
                const id = $videojs.attr('id')
                const player = videojs.getPlayer(id)
                // console.log('chi:', player.getChild('controlBar').children())
                player.getChild('controlBar').addChild('PtzButton', {}, 0)
                if ($('.vjs-stretch-control').length > 1) {
                    $('.vjs-stretch-control').eq(0).remove()
                }
                if ($('.vjs-snapshot-control').length > 1) {
                    $('.vjs-snapshot-control').eq(0).remove()
                }
            }, 200)
        },
        onPlay (e) {
          console.log('onPlay:', e)
          this.addControlBtn()
        },
        onPlayEnded (e) {
            console.log('onPlayEnded:', e)
        },
        onPlayError (e) {
            console.log('onPlayError:', e)
        },
        changePlay (e) {
            this.playUrl = this.playUrls[parseInt(e.target.value)]
        },
        copyPlayUrl (index) {
            this.copy(this.playUrls[index])
        },
        copy (copyContent) {
            const textareaEl = document.createElement('textarea')
            textareaEl.setAttribute('readonly', 'readonly')
            textareaEl.value = copyContent
            document.body.appendChild(textareaEl)
            textareaEl.select()
            const isSuccess = document.execCommand('copy')
            this.$message.success('复制成功!!!')
            return isSuccess
        },
        onClickCenter () {
            console.log('stop')
            this._ptzCtrl(this.ptzCommands.stop)
        },
        async onClickTop () {
            console.log('top')
            this._ptzCtrl(this.ptzCommands.up)
        },
        async onClickRight () {
            console.log('right')
            this._ptzCtrl(this.ptzCommands.right)
        },
        async onClickBottom () {
            console.log('bottom')
            this._ptzCtrl(this.ptzCommands.down)
        },
        async onClickLeft () {
            console.log('left')
            this._ptzCtrl(this.ptzCommands.left)
        },
        async _ptzCtrl (command) {
            if (this.ptzRequested) {
                return false
            }

            this.ptzRequested = true
            const result = await ptzCtrl(this.mainId, command)
            this.ptzRequested = false
            this.ptzActiveClass = ''

            return result
        },
        async liveUrl () {
          const { playUrls } = await liveUrl(this.mainId)
          this.playUrls = playUrls
          this.playUrl = this.playUrls[parseInt(this.mode)]
        }
    },
    computed: {
        title () {
            return this.$route.meta.title
        },
        mainId () {
            return this.$route.params.id
        },
        ptzCommands () {
            return this.$store.state.ptzCommands
        }
    },
    watch: {
        $route: {
            handler: (val, oldVal) => {
                console.log(val, oldVal)
            },
            // 深度观察监听
            deep: true
        },
        mode (val, oldVal) {
          // if (val !== oldVal) {
          //   this.addControlBtn()
          // }
        }
    }
}
</script>

<style lang="less" scoped>
.title {
    color: rgba(0, 0, 0, 0.85);
    font-size: 16px;
    font-weight: 500;
    margin-bottom: 16px;
}
.video-play {
    min-height: 300px;
    .easy-player {
        height: 720px !important;
    }
    .control {
        width: 75%;
        margin: 0 auto;
    }
    .form-group {
        width: 500px;
        margin: 10px 0 10px 30px;
    }
}
</style>
